.dytb{
    display: table !important;
}
.dytbc{
    display: table-cell !important;
    cursor: pointer;
    font-size: 16px;
    vertical-align: middle !important;
}
#AuthorEllipseCircle
{
    font-Size:10px;
    display: inline-block;
     border-radius: 60px;

    border:1px solid black;
    box-shadow: 0px 0px 2px #888;
       /* padding: 13px; */
    
       width: 24px;
       text-align: center;
       text-decoration: none;
       padding: 6.5px 5px 5px 5px; 
}
.dropdown-menu{
    margin-left: -9vw;
}
::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}
a:hover{
    color: #008081 !important;
    /* font-size: 17px; */
}
.rotate{
    transform:rotate(270deg);
    height:110px;
    width:123px;
    font-size: 20px;
    /* top:50%; */
    position:fixed;
    margin:80px auto;
}
#secondColumn
{
    padding-top: 25px;
}
@media only screen and (max-width: 768px) {
    #authorheader{
        /* height:45px; */
        width: 92%;
        top:92px;
    }
    #top{
        
        margin-top: 100px
    }
    #space{
        
        width: auto;
        width: 100vw;
        top:88px
    }
    
}
@media only screen and (min-width: 768px) {
    #authorheader{
        min-width:518px;
        max-width:518px;
        top:52px;
    }
    #top{
        margin-top: 62px;
    }
    #secondColumn{
        min-width:550px;
        max-width:550px; 
    }
    #space{
        min-width:550px;
        max-width:550px;
        top:40px
    }
}